.item{display:flex;justify-content:space-between;padding:10px 15px}.item.focus{border-top:1px solid #c5c5c5;border-bottom:1px solid #c5c5c5}.item:hover{border-top:1px solid #c5c5c5;border-bottom:1px solid #c5c5c5}.item.disabled input{background:#fff}.item.disabled:hover{border:none}.item input{width:100%;border:none}.item .remove{width:50px;height:16px;cursor:pointer;background:url("https://img.icons8.com/material/24/666666/delete-sign.png");background-size:cover}.item .remove:hover{background:url("https://img.icons8.com/material/24/000000/delete-sign.png");background-size:cover}
